/* Global Styles */
body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(to right, #f0f8ff, #e6f7ff);
  color: #333;
  margin: 0;
  padding: 0;
}

#headerPortion{
  background-color: #96d1f8;
}

/* Headers */
h1, h2 {
  color: #03426b;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2rem;
}

/* Main Body */
#mainBody {
  background-color: #ffffff;
  padding: 20px 50px;
  margin: 30px auto;
  max-width: 900px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  line-height: 1.6;
}

/* Paragraphs */
p {
  margin-bottom: 20px;
  font-size: 1rem;
  line-height: 1.8;
  color: #555;
}

/* Footer */
footer {
  background-color: #03426b;
  color: white;
  padding: 30px 0;
  text-align: center;
  font-size: 1rem;
}

#footerPortion {
  color: white;
}

/* Links */
a {
  color: #03426b;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
  color: #046494;
}

/* Lists */
ul {
  list-style-type: disc;
  margin: 20px auto;
  padding-left: 40px;
}

